@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.2/reset.min.css";
@import "https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap";
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: #0fff50;
}

:root {
  --color-neon-green: #0fff50;
  --tablet-screen-width: 768px;
}

*, :after, :before {
  box-sizing: border-box;
}

::selection {
  color: #000;
  background-color: var(--color-neon-green);
}

html {
  font-optical-sizing: auto;
  font-variant-ligatures: none;
  color: var(--color-neon-green);
  background-color: #000;
  font-family: JetBrains Mono, monospace;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

code {
  line-height: initial;
}

li:not(:last-child) {
  margin-bottom: 5px;
}

a {
  color: inherit;
  cursor: pointer;
  word-break: break-all;
  overflow-wrap: break-word;
  text-decoration: none;
}

a[target="_blank"] {
  cursor: alias;
}

a:focus, a:hover, a:active {
  color: #000;
  background-color: var(--color-neon-green);
  outline: 0;
  text-decoration: none !important;
}

.terminal, .cowsay {
  max-width: var(--tablet-screen-width);
  margin-top: 1em;
  margin-bottom: 1em;
}

.section > * {
  margin-bottom: 1em;
}

.command {
  margin-left: 5px;
  margin-right: 5px;
}

.blinker {
  opacity: 1;
  border-left: 7px solid #0fff50;
  height: 15px;
  margin-bottom: -2px;
  margin-left: -5px;
}

.blinker.blinking {
  animation: .9s steps(2, start) infinite blinking;
}

@keyframes blinking {
  to {
    visibility: hidden;
  }
}

@media (width <= 425px) {
  .ref {
    display: none;
  }
}

@media (width <= 500px) {
  .sha {
    display: none;
  }
}

.visually-hidden {
  clip-path: inset(50%);
  white-space: nowrap;
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
}
